import { ApolloClient } from "apollo-client"
import { HttpLink } from "apollo-link-http"
import { InMemoryCache } from "apollo-cache-inmemory"
import gql from "graphql-tag"
import * as d3 from "d3"
import config from "../config"
import topresults from "./results.json"
import wc011 from "./wc011.json"
import wcmunis from "./wcmunis.json"

const client = new ApolloClient({
  link: new HttpLink({ uri: "http://elections2019.xyz/graphql", useGETForQueries: true, headers: {"Content-Type" : "application/graphql" } }),
  cache: new InMemoryCache()
})

export function getVotesDataT(options) {

  var results;
  if (options.regionType == "municipality-vd") {
    results = JSON.parse(JSON.stringify(wc011));
    console.log("results", results);
    results["data"]["allProvincialBallots"]["edges"] = results["data"]["allProvincialBallots"]["edges"].filter(edge => {
      return (edge["node"]["location"]["vdNumber"] == options.regionName);
    }) 
    results["data"]["allProvincialBallots"]["edges"][0]["node"]["topResult"]["edges"] 
        = results["data"]["allProvincialBallots"]["edges"][0]["node"]["topResult"]["edges"].slice(0, options.numParties)
  } else if (options.regionType == "municipality"){
    results = JSON.parse(JSON.stringify(wcmunis));
    console.log("results", results);
    results["data"]["allMunicipalBallots"]["edges"] = results["data"]["allMunicipalBallots"]["edges"].filter(edge => {
        return (edge["node"]["location"]["name"].indexOf(options.regionName) != -1);
    })
    if (results["data"]["allMunicipalBallots"]["edges"].length) {
      results["data"]["allMunicipalBallots"]["edges"][0]["node"]["partyResults"]["edges"] 
        = results["data"]["allMunicipalBallots"]["edges"][0]["node"]["partyResults"]["edges"].slice(0, options.numParties)
    }
  } else {
    results = JSON.parse(JSON.stringify(topresults));
    console.log("results", results);
    results["data"]["allProvincialBallots"]["edges"] = results["data"]["allProvincialBallots"]["edges"].filter(edge => {
      if (options.regionType == "national") {
        return (edge["node"]["location"]["name"].indexOf("Country") != -1);
      } else if (options.regionType == "province") {
        return (edge["node"]["location"]["name"] == options.regionName);
      }
    })
    if (results["data"]["allProvincialBallots"]["edges"].length) {
      results["data"]["allProvincialBallots"]["edges"][0]["node"]["partyResults"]["edges"] 
        = results["data"]["allProvincialBallots"]["edges"][0]["node"]["partyResults"]["edges"].slice(0, options.numParties)
    }
  }

  return new Promise(function(resolve, reject) {

    resolve(results);
  });
}

export function getVotesDataM(options) {
  if (options.regionType == "national") {
    return client.query({
      query: gql`
      {
        allProvincialBallots(location_Name_Icontains:"Country"){
          edges{
            node{
              topResult(last:${options.numParties}) {
                edges{
                  node{
                    validVotes
                    percOfVotes
                    party {
                      id
                      name
                      abbreviation
                    }
                  }
                }
                
              }
              location {
                id
                name
              }
            }
          }
        }
      }
      `
    })
  } else if (options.regionType == "province") {
    return client.query({
      query: gql`
      {
        allProvincialBallots(location_Name_Icontains:"${options.provinceName}"){
          edges{
            node{
              topResult(last:${options.numParties}) {
                edges{
                  node{
                    validVotes
                    percOfVotes
                    party {
                      id
                      name
                      abbreviation
                    }
                  }
                }
              }
              location {
                id
                name
              }
            }
          }
        }
      }
      `
    })
  } else if (options.regionType == "municipality") {
    return client.query({
      query: gql`
      {
        allMunicipalBallots(location_Province_Name:"${options.provinceName}", 
          event_Description:"2014 National Election",
          location_Name: "${options.muniName}"
        ) {
          edges{
            node {
              topResult(last:${options.numParties}){
                edges{
                  node{
                    party {               
                      name
                      abbreviation
                    }
                    validVotes
                    percOfVotes
                  }
                }
                
              }
              location {
                code
                name
                longName
              }
            }
          }
        }
      }
      `
    })
  } else if (options.regionType == "municipality-vd") {
    return client.query({
      query: gql`
      {
        allVotingDistrictBallots(location_Id:"${options.vdNumber}", 
        event_Description_Icontains:"2014", 
        location_Ward_Municipality_Name_Icontains:"${options.muniCode}") {
          edges{
            node{
              location {
                vdNumber
              }
              topResult(last:${options.numParties}) {
                edges{
                  node{
                    party{
                      name
                    }
                    percOfVotes
                  }
                }  
              }
            }
          }
        }
      }
      `
    })


  }
}

export function getVotesData(options) {
    return client.query({
        query: gql`
        {
          allProvincialBallots(
              event_Description_Icontains:"2004", 
            event_EventType_Description_Icontains:"${options.regionType}",
            location_Name:"${options.regionName}"
        
          ) {
            edges {
              node {
                topResult(validVotes_Gt:1000, last:${options.numParties}) {
                  edges {
                    node {
                      validVotes
                      party {
                        name
                        abbreviation
                      }
                    }
                  }
                }
                location {
                  name
                }
              }
            }
          }
        }
        `,
      });
}